@import './../../../styles/variables';

.card {
  position: relative;
  width: 410px;
  height: 410px;
  padding: 30px 30px 20px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid transparent;
  border-radius: 20px;
  background-image: url('./../../../assets/img/squares.png');
  line-height: 26px;
  transition: $t-normal;
  box-shadow: 0px 4px 20px #57627333;

  @media screen and (max-width: 500px) {
    width: 326px;
    padding: 16px;
  }
}

.card:hover {
  background-image: none;
  background-color: $c-yellow;
  border-color: $c-black;
  transition: $t-normal;
}

.pencil {
  position: absolute;
  width: 56px;
  height: 40px;
  top: 16px;
  right: 21px;

  @media screen and (max-width: 500px) {
    width: 46px;
    height: 34px;
  }
}

.pencilShadow {
  position: absolute;
  width: 60px;
  height: 46px;
  top: 13px;
  right: 19px;

  @media screen and (max-width: 500px) {
    width: 50px;
    height: 38px;
    top: 14px;
  }
}

.container {
  display: flex;
  gap: 58px;
  align-items: flex-end;
  transition: $t-normal;
}

.title {
  font-weight: $fw-bold;
  margin-bottom: 25px;
}

.duration {
  font-family: $ff-secondary;
  font-size: $fs-navigation;
  margin-bottom: 10px;

  @media screen and (max-width: 500px) {
    font-size: $fs-bigger-text;
  }
}

.book {
  font-size: $fs-little-text;
  font-weight: $fw-medium;

  @media screen and (max-width: 500px) {
    font-size: $fs-very-little;
  }
}

.description {
  font-weight: $fw-medium;
  font-size: $fs-little-text;

  @media screen and (max-width: 500px) {
    font-size: $fs-very-little;
  }
}

.priceContainer {
  margin-bottom: 10px;
}

.text {
  margin-bottom: 7px;

  @media screen and (max-width: 500px) {
    font-size: $fs-little-text;
  }
}

.price {
  font-size: $fs-course-title;
  font-weight: $fw-semi-bold;
}

.currency {
  font-size: $fs-little-text;
  font-weight: $fw-medium;
}

.button {
  margin-top: 13px;
  width: 100%;
  max-width: 350px;
  padding: 18px;
  border: 1px solid $c-black;
  border-radius: 40px;
  cursor: pointer;
  background-color: transparent;
  font-family: $ff-secondary;
  font-size: $fs-navigation;
  font-weight: $fw-bold;
  transition: $t-normal;
}


