.wrapper{padding-top:82px;padding-bottom:46px;padding-left:0;padding-right:0}.heading{margin-bottom:42px}.card{width:100%;max-width:330px;height:100%;display:flex;flex-direction:column;align-items:center}.imgContainer{position:relative;border:2px solid rgba(0,0,0,0);width:100%;max-width:306px;height:420px;transition:.35s cubic-bezier(0.4, 0, 0.2, 1);border-radius:40px}.imgContainer img{border-radius:40px}.info{position:absolute;bottom:0;left:0;right:0;text-align:center;width:100%;max-width:306px;overflow:hidden;height:35px;color:rgba(0,0,0,0);background-color:rgba(0,0,0,0);border-bottom-left-radius:40px;border-bottom-right-radius:40px;font-size:14px;line-height:26px;transition:.35s cubic-bezier(0.4, 0, 0.2, 1)}.text{margin:0 auto;width:260px}.imgContainer:hover .info{color:#000;background-color:#fff108;height:182px;padding:14px 20px}.imgContainer:hover{border:2px solid #000}.descriptionContainer{margin-top:17px;line-height:26px}.name{font-size:24px;font-weight:500;text-shadow:2px 2px #fff108;text-align:center;margin-bottom:14px}.item{list-style-type:none;font-weight:300}.item:before{content:"·";font-size:20px;font-weight:600;padding-right:10px;vertical-align:middle}