.imgContainer{width:100%;max-width:400px}.teacherContainerLeft{display:flex;justify-content:space-between;margin:50px}.teacherContainerRight{display:flex;flex-direction:row-reverse;justify-content:space-between;margin:50px}.title{font-size:32px;font-weight:700;text-align:center;margin-bottom:50px}.infos{width:100%;max-width:850px;display:flex;flex-direction:column;justify-content:start;align-items:center}.teacherDescription{width:100%}.name{font-size:24px;line-height:26px;text-align:center;font-weight:500;text-shadow:2px 2px #fff108}.text{line-height:26px;text-align:center;margin-bottom:20px}.line{background-color:rgba(0,0,0,.1);width:100%;height:1px;margin-bottom:50px}.tabs{width:100%;display:flex;justify-content:center;gap:40px;margin-bottom:30px}@media screen and (max-width: 500px){.tabs{gap:10px}}.el,.element{list-style-type:none;width:100%}.el{font-family:"Caveat",cursive;width:fit-content;font-weight:700;font-size:20px;padding:12px 26px;background-color:#fff;height:45px;display:flex;justify-content:center;align-items:center;border:1px solid #000;border-radius:40px;transition:.35s cubic-bezier(0.4, 0, 0.2, 1);cursor:pointer}@media screen and (max-width: 500px){.el{padding:16px 10px}}.el:hover{outline:1px solid #000}.clicked{background-color:#fff108;outline:1px solid #000}.item{margin-left:10px;list-style-type:none}@media screen and (max-width: 500px){.item{margin-left:0}}.item:before{content:"·";vertical-align:middle;font-size:20px;padding-right:10px}.teacherTitle{display:flex;justify-content:center;align-items:center;gap:10px;margin-bottom:20px}.icon{width:40px;height:40px}