// ! Teenagers Slider
.teenagerSwiper {
  width: fit-content;
  height: 500px;
  padding-top: 10px;
}

.teenagerSwiper .swiper-wrapper {
  // transform: translate3d(0px, 0, 0);
  @media screen and (min-width: 700px) {
    justify-content: center;
  }
}

.teenagerSwiper .swiper-slide {
  max-width: 410px;

  @media screen and (max-width: 500px) {
    max-width: 326px;
    // padding: 16px;
    margin: 0 auto;
  }
}

.teenagerSwiper .swiper-slide:first-of-type {
  margin-left: 20px;
}

.teenagerSwiper .swiper-slide:last-of-type {
  margin-right: 20px;
}

.teenagerSwiper .swiper-button-prev {
  @media screen and (max-width: 500px) {
    left: 35%;
    top: 94%;
  }

  @media screen and (min-width: 500px) and (max-width: 600px) {
    left: 38%;
    top: 94%;
  }

  @media screen and (min-width: 600px) and (max-width: 750px) {
    left: 41%;
    top: 94%;
  }

  @media screen and (min-width: 750px) and (max-width: 900px) {
    left: 42%;
    top: 94%;
  }

  @media screen and (min-width: 900px) and (max-width: 1200px) {
    left: 44%;
    top: 94%;
  }
  @media screen and (min-width: 1200px) and (max-width: 1400px) {
    left: 45%;
    top: 94%;
  }
}

.teenagerSwiper .swiper-button-next {
  @media screen and (max-width: 500px) {
    right: 35%;
    top: 94%;
  }

  @media screen and (min-width: 500px) and (max-width: 600px) {
    right: 38%;
    top: 94%;
  }

  @media screen and (min-width: 600px) and (max-width: 750px) {
    right: 41%;
    top: 94%;
  }

  @media screen and (min-width: 750px) and (max-width: 900px) {
    right: 42%;
    top: 94%;
  }

  @media screen and (min-width: 900px) and (max-width: 1200px) {
    right: 44%;
    top: 94%;
  }
  @media screen and (min-width: 1200px) and (max-width: 1400px) {
    right: 45%;
    top: 94%;
  }
}

